// Button component - Mac OS 9 style
// Implements classic raised bevel button with all states

import React, { forwardRef, ButtonHTMLAttributes } from 'react';
import styles from './Button.module.css';

export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
	/**
	 * Button variant
	 * @default 'default'
	 */
	variant?: 'default' | 'primary' | 'danger';

	/**
	 * Button size
	 * @default 'md'
	 */
	size?: 'sm' | 'md' | 'lg';

	/**
	 * Whether the button is disabled
	 * @default false
	 */
	disabled?: boolean;

	/**
	 * Whether the button should take full width
	 * @default false
	 */
	fullWidth?: boolean;

	/**
	 * Additional CSS class names
	 */
	className?: string;

	/**
	 * Button content
	 */
	children: React.ReactNode;
}

/**
 * Mac OS 9 style Button component
 * 
 * Implements the classic raised bevel button with:
 * - 3-layer bevel effect (highlight, shadow, drop shadow)
 * - Pressed/inset state on active
 * - Keyboard focus indicators
 * - Proper ARIA attributes for accessibility
 * 
 * @example
 * ```tsx
 * <Button onClick={handleClick}>Click Me</Button>
 * <Button variant="primary" size="lg">Primary Action</Button>
 * <Button disabled>Disabled Button</Button>
 * ```
 */
export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
	(
		{
			variant = 'default',
			size = 'md',
			disabled = false,
			fullWidth = false,
			className = '',
			children,
			type = 'button',
			...props
		},
		ref
	) => {
		const classNames = [
			styles.button,
			styles[`button--${variant}`],
			styles[`button--${size}`],
			fullWidth && styles['button--full-width'],
			disabled && styles['button--disabled'],
			className,
		]
			.filter(Boolean)
			.join(' ');

		return (
			<button
				ref={ref}
				type={type}
				className={classNames}
				disabled={disabled}
				aria-disabled={disabled}
				{...props}
			>
				{children}
			</button>
		);
	}
);

Button.displayName = 'Button';

export default Button;